<template>
      <div class="showOrHideBtn" @click="showOrHideClick">
        <i class="ivu-icon ivu-icon-ios-arrow-down" v-show="!showMore"></i>
        <i class="ivu-icon ivu-icon-ios-arrow-up" v-show="showMore"></i>
        <span class="moreText" v-show="!showMore">展开</span>
        <span class="moreText" v-show="showMore">收起</span>
      </div>
</template>
<script>
  export default {
    data () {
      return {
        showMore:false,
      }
    },
    mounted() {
      this.$nextTick(function () {
       })
    },
    //测试无用
    props:{
      sendNum:[Number,String]
    },
    methods: {
      //查询条件的显示隐藏 将showMore 传递给父组件
      showOrHideClick(){
        this.showMore=!this.showMore
        this.$emit("show-hide",this.showMore)
      },
    },
  }
</script>
<style lang="less" scoped>
  .showOrHideBtn{
    text-align: center;
  }
  .moreText{
    font-size:12px;
    color:#999;
    margin-left:5px;
  }
</style>



