<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">换点流水号：</span>
            <Input v-model="pointSerialNum" placeholder="换点流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">积分商流水号：</span>
            <Input v-model="integralSerialNum" placeholder="积分商流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">消费者账号：</span>
            <Input v-model="userNum" placeholder="消费者账号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">积分：</span>
            <Input v-model="integral" placeholder="积分" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">关联单号：</span>
            <Input v-model="relevanceNum" placeholder="关联单号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">业务类型：</span>
            <Select v-model="businessType" class="input-width">
              <Option v-for="item in businessTypeList" :value="item.child_value" :key="item.child_value" class="input-width">{{ item.child_name }}</Option>
            </Select>
          </div>
        </Col>
        <Col span="10" v-show="showMore">
          <div  class="input-item">
            <daterange :name="'记账日期'" ref="regTime"  @start-time="timeChange('tallyStart',$event)"  @end-time="timeChange('tallyEnd',$event)"></daterange>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="reSearchInfo" >查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
      <showbtn :sendNum="10" @show-hide="showMore=$event"></showbtn>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap" >
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border>
        <div slot="header">
          <div class="table-title">累计变动数量 {{allAmount}} </div>
        </div>
      </Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import showbtn from '../../components/showbtn'
  import { tableMethods } from "../../../js/mixintable";
  import daterange from '../../components/daterange'
  export default {
    data () {
      return {
        /*搜索区域 条件*/
        pointSerialNum:'', //换点流水号
        integralSerialNum:'', //积分商流水号
        userNum:'',//消费者账号
        integral:'',// 积分
        relevanceNum:'',//关联单号
        businessType:'',//业务类型
        tallyStartTime:'',//记账开始时间
        tallyEndTime:'',// 记账结束时间
        allAmount:'0',//累计变动数量
        loading:false,
        businessTypeList:[
          {
            child_name: '全部',
            child_value: ''
          }
        ],
        /*table表格*/
        tableColumns: [
          {
            title: '序号',
            align: 'center',
            width:60,
            key:'indexs'
          },
          {
            title: '换点流水号',
            key: 'ban',
            align: 'center',
            width:100
          },
          {
            title: '积分商流水号',
            key: '',//空着
            align: 'center',
            minWidth: 100,
          },
          {
            title: '消费者账号',
            align: 'center',
            key: 'user_no',
            minWidth: 150,
          },
          {
            title: '积分ID',
            align: 'center',
            key: 'point_cv',
            minWidth: 100,
          },
          {
            title: '积分名称',
            align: 'center',
            key: 'point_name',
            minWidth: 150,
          },
          {
            title: '变动数量',
            align: 'center',
            key: 'amount',//根据积分精度转换
            minWidth: 100,
          },{
            title: '变前数量',
            align: 'center',
            key: '',//空着
            minWidth: 100,
          },{
            title: '变后数量',
            align: 'center',
            key: '',//空着
            minWidth: 100,
          },
          {
            title: '关联单号',
            align: 'center',
            key: 'bn',
            minWidth: 100,
          },
          {
            title: '业务类型',
            align: 'center',
            key: 'b_name',
            minWidth: 150,
          },
          {
            title: '记账时间',
            align: 'center',
            key: 'account_time',
            minWidth: 180,
          }]
      }
    },
    components: {
      daterange,
      showbtn
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //获取业务类型
        this.getBusinessTypeList()
        //查询数据
        //this.searchInfo();
      })
    },
    methods: {
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.pointSerialNum=''// 换点流水号
        this.integralSerialNum='' //积分商流水号
        this.userNum=''//消费者账号
        this.integral=''//积分
        this.relevanceNum='' //关联单号
        this.businessType='' //业务类型
        this.$refs.regTime.resetTime()
        this.tallyStartTime='' //记账开始时间
        this.tallyEndTime='' //记账结束时间
      },
      //获取业务类型
      getBusinessTypeList(){
        let jsonData = protocol.getJsonParams(protocol.queryCodeAccount, {});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.businessTypeList = this.businessTypeList.concat( data.returnList);
        })
      },
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        let objData={
          ban:this.pointSerialNum,// 换点流水号
          // this.integralSerialNum, //积分商流水号
          user_no:this.userNum,//消费者账号
          point:this.integral,//积分
          bn:this.relevanceNum, //关联单号
          b_cv:this.businessType, //业务类型
          min_account_time:this.tallyStartTime.length>0?this.tallyStartTime.replace(/-/g,''):'',
          max_account_time:this.tallyEndTime.length>0?this.tallyEndTime.replace(/-/g,''):'',
          pPage:this.page,
          pSize:this.size
        };
        let jsonData = protocol.getJsonParams(protocol.queryPointBan, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.$Loading.finish();
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
          this.allAmount = data.sumAmount;
        })
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="tallyStart"){
          this.tallyStartTime=time
        }else if(e=="tallyEnd"){
          this.tallyEndTime=time
        }
      },
      //导出数据
      exportOk(){
        let objData={
          ban:this.pointSerialNum,// 换点流水号
          // this.integralSerialNum, //积分商流水号
          user_no:this.userNum,//消费者账号
          point:this.integral,//积分
          bn:this.relevanceNum, //关联单号
          b_cv:this.businessType, //业务类型
          min_account_time:this.tallyStartTime.length>0?this.tallyStartTime.replace(/-/g,''):'',
          max_account_time:this.tallyEndTime.length>0?this.tallyEndTime.replace(/-/g,''):'',
        };
        let jsonData = protocol.getJsonParams(protocol.exportPointBan, objData);
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    },
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
  .table-title{
    background-color: #F8F8F9;
    height:47px;
    padding:0 20px;
    font-weight: 700;
    text-align: center;
  }
</style>



